.todo {
  background-color: rgb(78, 7, 78);
  color: white;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.16);
  width: 430px;
  font-size: 24px;
  margin-bottom: 10px;
  padding: 16px;
  font-weight: bold;
}

.todo-text {
  margin-left: 10px;
}

.todo-icon {
  float: right;
  cursor: pointer;
}
